<template>
    <div>
        <!--        面包屑导航-->
        <el-breadcrumb  separator-class="el-icon-arrow-right" style="margin: 20px">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>{{this.$store.state.adminMenuList.adminMenuList[3].menuName}}</el-breadcrumb-item>
            <el-breadcrumb-item>{{this.$store.state.adminMenuList.adminMenuList[3].children[0].menuName}}</el-breadcrumb-item>
        </el-breadcrumb>

        <!-- 卡片视图区域 -->
        <el-card>

            <!-- 搜索与添加区域 -->
            <el-row :gutter="20">
                <el-col :span="8">
                    <el-input placeholder="请输入内容" v-model="queryInfo.query" clearable @clear="getUserList">
                        <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
                    </el-input>
                </el-col>
                <el-col :span="4">
                    <el-button type="primary" @click="addDialogVisible = true">添加用户</el-button>
                </el-col>
            </el-row>

            <!-- 用户列表区域 -->
            <el-table :data="userlist" border stripe>
                <el-table-column type="index" label="#" prop="id"></el-table-column>
                <el-table-column label="姓名" prop="username"></el-table-column>
                <el-table-column label="邮箱" prop="email"></el-table-column>
                <el-table-column label="电话" prop="phone"></el-table-column>
                <el-table-column label="角色" prop="role"></el-table-column>
                <el-table-column label="状态">
                    <template slot-scope="scope">
                        <el-switch v-model="scope.row.state" @change="userStateChanged(scope.row)" :active-value="'true'" :inactive-value="'false'">
                        </el-switch>
                    </template>
                </el-table-column>
                <el-table-column label="操作" width="180px">
                    <template slot-scope="scope">
                        <!-- 修改按钮 -->
                        <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditDialog(scope.row.id)"></el-button>
                        <!-- 删除按钮 -->
                        <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeUserById(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!-- 分页区域 -->
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="queryInfo.pagenum" :page-sizes="[1, 2, 5, 10]" :page-size="queryInfo.pagesize" layout="total, sizes, prev, pager, next, jumper" :total="total">
            </el-pagination>
        </el-card>

        <!-- 添加用户的对话框 -->
        <el-dialog title="添加用户" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
            <!-- 内容主体区域 -->
            <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="addForm.username"></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="addForm.password"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="addForm.email"></el-input>
                </el-form-item>
                <el-form-item label="手机" prop="phone">
                    <el-input v-model="addForm.phone"></el-input>
                </el-form-item>
            </el-form>
            <!-- 底部区域 -->
            <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addUser">确 定</el-button>
      </span>
        </el-dialog>

        <!-- 修改用户的对话框 -->
        <el-dialog title="修改用户" :visible.sync="editDialogVisible" width="50%" @close="editDialogClosed">
            <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
                <el-form-item label="用户名" prop="username">
                    <el-input v-model="editForm.username" ></el-input>
                </el-form-item>
                <el-form-item label="密码" prop="password">
                    <el-input v-model="editForm.password"></el-input>
                </el-form-item>
                <el-form-item label="邮箱" prop="email">
                    <el-input v-model="editForm.email"></el-input>
                </el-form-item>
                <el-form-item label="手机" prop="phone">
                    <el-input v-model="editForm.phone"></el-input>
                </el-form-item>
                <el-form-item label="角色" prop="role">
                    <el-select v-model="editForm.role">
                        <el-option  v-for="item in rolesList" :key="item.value" :label="item.label" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editUserInfo">确 定</el-button>
      </span>
        </el-dialog>


    </div>
</template>

<script>
    export default {
        name: "EditUser",
        data(){
            // 验证邮箱的规则
            let checkEmail = (rule, value, cb) => {
               if(this.addForm.email||this.editForm.email){
                   // 验证邮箱的正则表达式
                   const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/

                   if (regEmail.test(value)) {
                       // 合法的邮箱
                       return cb()
                   }

                   cb(new Error('请输入合法的邮箱'))
               }
               else cb()
            }

            // 验证手机号的规则
            let checkPhone = (rule, value, cb) => {
               if(this.addForm.phone||this.editForm.phone){
                   // 验证手机号的正则表达式
                   const regPhone = /^[1](([3][0-9])|([4][5-9])|([5][0-3,5-9])|([6][5,6])|([7][0-8])|([8][0-9])|([9][1,8,9]))[0-9]{8}$/

                   if (regPhone.test(value)) {
                       //合法手机号
                       return cb()
                   }

                   cb(new Error('请输入合法的手机号'))
               }
               else cb()
            }

            return {
                // 获取用户列表的参数对象
                queryInfo: {
                    query: '',
                    // 当前的页数
                    pagenum: 1,
                    // 当前每页显示多少条数据
                    pagesize: 5
                },
                userlist: [],
                //总条数
                total: 0,
                // 控制添加用户对话框的显示与隐藏
                addDialogVisible: false,
                // 添加用户的表单数据
                addForm: {
                    username: '',
                    password: '',
                    email: '',
                    phone: ''
                },
                // 添加表单的验证规则对象
                addFormRules: {
                    username: [
                        { required: true, message: '请输入用户名', trigger: 'blur' },
                        {
                            min: 3,
                            max: 10,
                            message: '用户名的长度在3~10个字符之间',
                            trigger: 'blur'
                        }
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' },
                        {
                            min: 6,
                            max: 15,
                            message: '用户密码的长度在6~15个字符之间',
                            trigger: 'blur'
                        }
                    ],
                    email: [
                        { validator: checkEmail, trigger: 'blur' }
                    ],
                    phone: [
                        { validator: checkPhone, trigger: 'blur' }
                    ]
                },
                // 控制修改用户对话框的显示与隐藏
                editDialogVisible: false,
                // 查询到的用户信息对象
                editForm: {},
                // 修改表单的验证规则对象
                editFormRules: {
                    username: [
                        { required: true, message: '请输入用户名', trigger: 'blur' },
                        {
                            min: 3,
                            max: 10,
                            message: '用户名的长度在3~10个字符之间',
                            trigger: 'blur'
                        }
                    ],
                    password: [
                        { required: true, message: '请输入密码', trigger: 'blur' },
                        {
                            min: 6,
                            max: 15,
                            message: '用户密码的长度在6~15个字符之间',
                            trigger: 'blur'
                        }
                    ],
                    email: [
                        { validator: checkEmail, trigger: 'blur' }
                    ],
                    phone: [
                        { validator: checkPhone, trigger: 'blur' }
                    ]
                },
                // 所有角色的数据列表
                rolesList: [
                    {value: 'admin', label: 'admin'},
                    {value: 'normal', label: 'normal'}
                ]
            }
        },
        created() {
            this.getUserList()
        },
        methods: {
            async getUserList() {
                 const res=await  this.$http.get('/userlist',{
                    params:this.queryInfo
                })
                this.userlist = res.data.userlist
                console.log(res.data.total)
                console.log(res.data.userlist[0].state)
                this.total = res.data.total
            },
            // 监听 pagesize 改变的事件
            handleSizeChange(newSize) {
                // console.log(newSize)
                this.queryInfo.pagesize = newSize
                this.getUserList()
            },
            // 监听 页码值 改变的事件
            handleCurrentChange(newPage) {
                console.log(newPage)
                this.queryInfo.pagenum = newPage
                this.getUserList()
            },
            // 监听 switch 开关状态的改变
            async userStateChanged(userinfo) {
                console.log(userinfo)
                const { data: res } = await this.$http.put(
                    `users/${userinfo.id}/state/${userinfo.state}`
                )
                console.log(res)
                if (res.status !== 200) {
                    userinfo.state = !userinfo.state
                    return this.$message.error('更新用户状态失败！')
                }
                this.$message.success('更新用户状态成功！')
            },
            // 监听添加用户对话框的关闭事件
            addDialogClosed() {
                this.$refs.addFormRef.resetFields()
            },
            // 点击按钮，添加新用户
            addUser() {
                this.$refs.addFormRef.validate(async valid => {
                    if (!valid) return this.$message.error('请填写正确的信息！')
                    // 可以发起添加用户的网络请求
                   const res = await this.$http.post('adduser', this.addForm)
                    console.log(res)
                    if (res.data.status !== 200) {
                        this.$message.error('添加用户失败！')
                    }
                    else this.$message.success('添加用户成功！')
                    // // 隐藏添加用户的对话框
                     this.addDialogVisible = false
                    // 重新获取用户列表数据
                    await this.getUserList()
                })
            },

            // 展示编辑用户的对话框
            async showEditDialog(id) {
                // console.log(id)
                const res = await this.$http.get('users/' + id)
                console.log(res)
                if (res.status !== 200) {
                    return this.$message.error('查询用户信息失败！')
                }

                this.editForm = res.data.user[0]
                this.editDialogVisible = true
            },

            // 监听修改用户对话框的关闭事件
            editDialogClosed() {
                this.$refs.editFormRef.resetFields()
            },

            // 修改用户信息并提交
            editUserInfo() {
                this.$refs.editFormRef.validate(async valid => {
                    if (!valid) return this.$message.error('请填写正确的信息！')
                    // 发起修改用户信息的数据请求
                    const res  = await this.$http.put('edituser' , this.editForm)
                    console.log(res)
                    if (res.data.status !== 200) {
                        return this.$message.error('更新用户信息失败！')
                    }
                    // 提示修改成功
                    else this.$message.success('更新用户信息成功！')
                    console.log('更新用户信息成功！')
                    // 关闭对话框
                     this.editDialogVisible = false
                    // 刷新数据列表
                    await this.getUserList()

                })
            },
            // 根据Id删除对应的用户信息
            async removeUserById(id) {
                // 弹框询问用户是否删除数据
                const confirmResult = await this.$confirm(
                    '此操作将永久删除该用户, 是否继续?',
                    '提示',
                    {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }
                ).catch(err => err)

                // 如果用户确认删除，则返回值为字符串 confirm
                // 如果用户取消了删除，则返回值为字符串 cancel
                // console.log(confirmResult)
                if (confirmResult !== 'confirm') {
                    return this.$message.info('已取消删除')
                }

                const res = await this.$http.delete('users/' + id)

                if (res.data.status !== 200) {
                    return this.$message.error('删除用户失败！')
                }
                this.$message.success('删除用户成功！')
                this.getUserList()
            },

        }
    }
</script>

<style scoped>

</style>
